<template>
  <v-form ref="form" v-model="valid" class="form">
    <v-row>
      <v-col cols="4">
        <v-text-field
          v-model="form.title"
          required
          :rules="rules.title"
          label="标题"
        />
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="8">
        <v-text-field
          v-model="form.subTitle"
          required
          :rules="rules.subTitle"
          label="副标题"
        />
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12">
        <mavon-editor
          v-model="form.content"
          :ishljs="false"
          class="editor"
          @imgAdd="handleImgAdd"
          @imgDel="handleImgDel"
        />
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12">
        <v-btn
          class="mr-4"
          @click="submit"
        >
          submit
        </v-btn>
      </v-col>
    </v-row>
    <v-overlay :value="overlay" />
  </v-form>
</template>

<script>
import { edit, add, view, upload } from '@/api/modules/article'

export default {
  name: 'ArticleEdit',
  data() {
    return {
      overlay: false,
      id: null,
      form: {
        title: '',
        subTitle: '',
        content: ''
      },
      selector: {
        tags: ['java', 'vue', 'java script', 'python', 'golang']
      },
      valid: false,
      rules: {
        title: [
          v => !!v || '请输入标题',
          v => v.length <= 16 || '标题长宽不能超过16位'
        ],
        subTitle: [
          v => !!v || '请输入副标题',
          v => v.length <= 120 || '副标题长度不能超过120位长度'
        ],
        content: [
          v => !!v || '请输入内容',
          v => v.length <= 5000 || '内容长度不能超过5000位长度'
        ]
      }
    }
  },
  created() {
    this.id = this.$route.params.id
    if (this.id) {
      this.init(this.id)
    }
  },
  methods: {
    init(id) {
      view({ id }).then(data => {
        this.form.title = data.data.title
        this.form.subTitle = data.data.subTitle
        this.form.content = data.data.content
      })
    },
    submitSuccess(data) {
      this.overlay = false
      if (data.data.code === 200) {
        const url = this.id ? `/article/${data.data.data}` : '/article'
        this.$router.push(url)
      }
    },
    submitException(e) {
      console.error(e)
      this.overlay = false
    },
    handleImgAdd(pos, file) {
      upload({ file }).then(data => {
        const url = data.data.uri
        let name = file.name
        if (name.includes('-')) {
          name = name.replace(/-/g, '')
        }
        const content = this.form.content
        // 第二步.将返回的url替换到文本原位置![...](0) -> ![...](url)  这里是必须要有的
        if (content.includes(name)) {
          const oStr = `(${pos})`
          const nStr = `(${url})`
          const index = content.indexOf(oStr)
          const str = content.replace(oStr, '')
          const insertStr = (source, start, newStr) => {
            return source.slice(0, start) + newStr + source.slice(start)
          }
          this.form.content = insertStr(str, index, nStr)
        }
        console.log(this.form.content)
      })
    },
    handleImgDel() {

    },
    submit() {
      this.$refs.form.validate()
      if (!this.valid) return

      this.overlay = true
      if (this.id) {
        edit({
          id: this.id,
          data: this.form
        }).then(data => {
          this.submitSuccess(data)
        }).catch(e => {
          this.submitException(e)
        })
      } else {
        add({ data: this.form })
          .then(data => {
            this.submitSuccess(data)
          }).catch(e => {
            this.submitException(e)
          })
      }
    }
  }
}
</script>

<style scoped>
.form .editor {
  z-index: 7;
}
</style>
